import { Component, OnInit } from '@angular/core';
import * as L from "leaflet";
import { GetNavigationService } from '../services/get-navigation.service';
import { MapService } from '../services/map.service';
import {site, parameters} from '../site';
import {myfunctions} from '../shared/myfunctions';
import { MatProgressButtonOptions } from 'mat-progress-buttons';
import { ModalComponent } from '../modal/modal.component';
import { MatDialog, MatButtonToggleDefaultOptions } from '@angular/material';
import { ErrorDialogService } from '../services/error-dialog.service';
import 'leaflet-search';

@Component({
  selector: "app-map",
  templateUrl: "./map.component.html",
  styleUrls: ["./map.component.css"]
})

export class MapComponent extends myfunctions implements OnInit {

  private map: L.map;

  constructor(
    public dialog: MatDialog,
    private _GetNavigationService: GetNavigationService,
    private _MapService: MapService,
    public errorDialogService: ErrorDialogService
  ) { super() }

  ngOnInit() {
    this._GetNavigationService.getRequiredConfig()
      .toPromise().then(data => {
        this.Site_reference = data['configuration'];
      }); //get service {description: Initial description}
    //this.newFunc(); moving layers control to the sidebar
  }

  onMapReady(map: L.map) {
    var search = L.control.search({
      url: 'https://nominatim.openstreetmap.org/search?format=json&q={s}',
      placeholder: 'Search',
      minNumberOfCharacters: 0,
      searchOnEnter: true,
      noResults: 'No Records Found',
      jsonpParam: 'json_callback',
      propertyName: 'display_name',
      propertyLoc: ['lat', 'lon'],
      marker: L.circleMarker([0, 0], { radius: 30 }),
      autoCollapse: true,
      autoType: false,
      minLength: 2,
      container: 'findbox'
    });
    search.addTo(map);
    this.map = map;
  }

  step = 0;

  setStep(index: number) {
    this.step = index;
  }

  nextStep() {
    this.step++;
  }

  prevStep() {
    this.step--;
  }

  handleEvent(map: L.map) {
    if (typeof this.map === 'undefined') { } else {
      if (this.step === 1 || this.step === 2) { } else {
        if (this.map.getZoom() <= 9) {
          this.setStep(0)
          this.checkZoom(this.map.getZoom())//, this.markers)
        } else if (this.map.getZoom() > 9) {
          this.setStep(1);
          //if (this.markers.length < 1) {
          //} else {
          this.checkZoom(this.map.getZoom())//, this.markers)
        }
        //} else {
        //  this.setStep (0)
        //}
      }
    }
  }

  checkZoom(zoom) { //, markers) {
    if (zoom > 9) { // && markers.length > 0) { //if there are more than 1 markers, zoom on
      this.barButtonOptions_downstream.disabled = false;
      this.barButtonOptions_upstream.disabled = true;
    } else {
      this.barButtonOptions_downstream.disabled = true;
      this.barButtonOptions_upstream.disabled = true;
    }
  }

  openDialog() {
    let dialog = this.dialog.open(ModalComponent, {
      width: '40%',
      height: '90%',
      disableClose: true
    });
    dialog.afterClosed().subscribe(result => {
      this.mapReady = true;
    });
  }

  layersControl = {
    baseLayers: {
      'OpenStreetMap': L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
        maxZoom: 20,
        zIndex: 1,
        attribution:
          'Imagery from <a href="https://giscience.uni-hd.de/">GIScience Research Group @ University of Heidelberg</a>' +
          '&mdash; Map data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a>'
      }),

      'Topo': L.tileLayer('https://server.arcgisonline.com/ArcGIS/rest/services/World_Topo_Map/MapServer/tile/{z}/{y}/{x}', {
        zIndex: 1,
        attribution:
          'Tiles &copy; Esri &mdash; Esri, DeLorme, NAVTEQ, TomTom, Intermap, iPC, USGS, FAO, NPS, NRCAN, GeoBase, Kadaster NL,' +
          'Ordnance Survey, Esri Japan, METI, Esri China (Hong Kong), and the GIS User Community'
      }),

      'TexasHydro': L.tileLayer(
        "https://txgeo.usgs.gov/arcgis/rest/services/Mapping/HydroBaseMapForTerrain/MapServer/tile/{z}/{y}/{x}",
        {
          maxZoom: 11,
          minZoom: 5,
          attribution: 'Texas Streamer Esri product'
        }
      )
    }
  }

  zoom = 4;
  center = L.latLng(40.0, -100.0);
  // Values to bind to Leaflet Directive
  options = {
    layers: [this.layersControl.baseLayers.Topo],
    zoom: this.zoom,
    center: this.center
  };

  markers: L.layer[] = [];
  Site_reference: site;
  results = [];
  sites_upstream = [];
  sites_downstream = [];
  bounds: any = null;
  marker_sites = [];
  nodemarker = [];
  mapReady: boolean = false;
  methodType: string = null;

  newFunc() {
    // Create the control and add it to the map;
    var control = L.control.layers(this.layersControl);
    var htmlObject = control.getContainer();
    var a = document.getElementById('example');
    function setParent(el, newParent) {
      newParent.appendChild(el);
    }
    setParent(htmlObject, a);
  }

  addMarker(e) {
    if (this.map.getZoom() > 9 && this.mapReady === true) {
      let mySpill = new site([this.Site_reference]);
      if (this.markers.length > 0) {
        while (this.markers.length != 0) {
          this.markers.splice(0, 1)
        }
      }
      const marker = new L.marker([e.latlng.lat, e.latlng.lng], {
        draggable: true,
        autoPan: true,
        icon: L.icon({
          iconUrl: "https://raw.githubusercontent.com/pointhi/leaflet-color-markers/master/img/marker-icon-2x-black.png",
          shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/0.7.7/images/marker-shadow.png',
          iconSize: [25, 41],
          iconAnchor: [12, 41],
          popupAnchor: [1, -34],
          shadowSize: [41, 41]
        })
      }).on('click', this.swapElement(mySpill['mylist'], 2, 3));
      L.DomUtil.removeClass(this.map._container, 'crosshair-cursor-enabled');
      this.mapReady = false;
      this.markers.push(marker);
      this.checkZoom(this.map.getZoom())//, this.markers)
      this._MapService.myPoint = marker;
      this._MapService.result = mySpill;
      if (this.methodType === "downstream") {
        let mySite = this._MapService.result;
        let e = this._MapService.myPoint.getLatLng();
        this.onMarkerClick(mySite['mylist'], e.lat, e.lng, 'downstream', ['streamStatsgage', 'flowline'], 1000);
        this.barButtonOptions_downstream.active = true;
      } else if (this.methodType === "upstream") {
        let mySite = this._MapService.result;
        let e = this._MapService.myPoint.getLatLng();
        this.onMarkerClick(mySite['mylist'], e.lat, e.lng, 'upstream', ['streamStatsgage'], 1000);
        this.barButtonOptions_upstream.active = true;
      }
    } else { }
  }

  barButtonOptions_downstream: MatProgressButtonOptions = {
    active: false,
    text: 'Spill Response',
    spinnerSize: 18,
    raised: true,
    stroked: false,
    buttonColor: 'primary',
    spinnerColor: 'accent',
    fullWidth: true,
    disabled: true,
    mode: 'indeterminate'
  }

  barButtonOptions_upstream: MatProgressButtonOptions = {
    active: false,
    text: 'Spill Planning',
    spinnerSize: 18,
    raised: true,
    stroked: false,
    buttonColor: 'primary',
    spinnerColor: 'accent',
    fullWidth: true,
    disabled: true,
    mode: 'indeterminate'
  }

  getUpstream() {
    this.mapReady = true;
    L.DomUtil.addClass(this.map._container, 'crosshair-cursor-enabled');
    this.methodType = "upstream";
  }

  getDownstream() {
    this.mapReady = true;
    L.DomUtil.addClass(this.map._container, 'crosshair-cursor-enabled');
    this.methodType = "downstream";
    this.barButtonOptions_downstream.buttonColor = 'accent';
  }

  onMarkerClick(e, lat, lng, cond, option, len) {
    e[0]['value']['coordinates'] = [lng, lat]; // add lat long
    e[1]['value'] = cond;
    e[3]['value'] = option;
    //for upstream only
    if (e[2].value instanceof Array) { //if array, set limit of 100, copy parameters into a tuple, else do nothing since it should be already set
      e[2].value[0].value = len;
      e[2].value.splice(1, 1);
      let myvar = new parameters(e[2].value[0]);
      e[2].value = myvar;
    }

    this._GetNavigationService.postGage(e)
      .toPromise().then(data => {
        let myreturn;
        let polyline;
        if (cond == 'upstream') {
          myreturn = this._MapService.getUpstream(data);
          this.barButtonOptions_upstream.active = false;
        } else {
          myreturn = this._MapService.getDownstream(data);
          var myStyle = {
            "color": "#FF3333",
            "weight": 3,
            "opacity": 0.60
          }
          this._MapService.addPolyLine(data);
          polyline = L.geoJSON(this._MapService.streamArray, { style: myStyle });
          if (typeof polyline === 'undefined') { } else {
            this.markers.push(polyline);
          }
        }
        this.markers.push(myreturn);
        this.barButtonOptions_downstream.active = false;
        this.barButtonOptions_downstream.disabled = true;
        var group = L.featureGroup(this.markers);
        this.map.fitBounds(group.getBounds());
        for (var i = 0; i < this._MapService.lastnode.length; i++) {
          this.markers.push(this._MapService.lastnode[i])
        }
        this.setStep(2);
      })
      .catch((err) => {
        console.log("error: ", err.message);
        this.sendAlert(err, "Navigation Services");
        this.barButtonOptions_downstream.active = false;
        this.barButtonOptions_downstream.buttonColor = 'primary';
        this.markers = [];
      });
    };


  sendAlert(err, serv) {
    let data = {};
    data = {
      //reason: error && error.error.reason ? error.error.reason : '',
      status: err.status,
      url: err.url,
      service: serv
    };
    this.errorDialogService.openDialog(data);
  }

  reset() {
    this.markers.length = 0;
    this._MapService.clear();
    this.setStep(0);
    this.barButtonOptions_downstream.active = false;
    this.barButtonOptions_upstream.active = false;
    this.map.flyTo([40.0, -100.0], 4);
  }
}
